@import "./Themes/Palettes.scss";

.Root {
  background-color: $DarkThemePrimaryColor;
}

.PageContainer {
  height: 100vh;
  width: 100vw;

  overflow-x: hidden;
  overflow-y: scroll;

  //font-size: 4vw;
  font-size: 5rem;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 10%;
}

.PageHeader {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 2%;
  font-size: clamp(0.75em, 2vw, 1.5em);
  // width: 100vh;
  position: sticky;
  top: 0px;
  padding: 1%;
}

.PageHeaderLink {
  color: $DarkThemeTextColor;
}
.RedHover:hover {
  color: $Red;
}
.OrangeHover:hover {
  color: $Orange;
}
.YellowHover:hover {
  color: $Yellow;
}
.GreenHover:hover {
  color: $Green;
}
.BlueHover:hover {
  color: $Blue;
}

.Section {
  min-height: 100vh;
  overflow: hidden;
  width: clamp(300px, 50%, 8000px);

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Introduction {
  color: $LightThemePrimaryColor;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10%;
}

.IntroductionPreIntro {
  font-size: clamp(0.175em, 2vw, 0.25em);
  color: $Red;
}

.IntroductionName {
  font-size: clamp(0.5em, 5vw, 1em);
}

.IntroductionOneLiner {
  font-size: clamp(0.35em, 3.75vw, 0.75em);
  color: $Red;
}

.IntroductionSummary {
  padding-top: 5%;
  font-size: clamp(0.2em, 2vw, 0.35em);
}

.IntroductionHireMe {
  font-size: clamp(0.25em, 3.75vw, 0.6em);
  padding-top: 5%;
  color: $DarkThemeAccentColor;

  @media screen and (max-width: 600px) {
    align-self: center;
    margin-top: 5%;
  }
}

.IntroductionResumeDownload {
  width: fit-content;
  color: $DarkThemeTextColor;
  font-size: clamp(0.175em, 2.5vw, 0.5em);

  padding: 1%;
  border-style: solid;
  border-width: 2px;
  border-radius: 15px;

  @media screen and (max-width: 600px) {
    align-self: center;
    margin: 1%;
    border-radius: 10px;
    padding: 2%;
  }
}

.IntroductionResumeDownload:hover {
  color: $Red;
}

.ResumeLink {
  font-size: inherit;
  color: inherit;
  width: fit-content;
}

.AboutMe {
  color: $LightThemePrimaryColor;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.AboutMeHeader {
  font-size: clamp(0.25em, 2vw, 0.5em);
  color: $Orange;
}

.AboutMeInfoContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2%;

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }
}

.AboutMeParagraph {
  font-size: clamp(0.125em, 1vw, 0.35em);
  width: 47.5%;
  .AboutMeAltColor {
    color: $Orange;
  }

  @media screen and (max-width: 600px) {
    width: 100%;
    padding-top: 2.5%;
  }
}

.AboutMeToolsHeader {
  color: $DarkThemeAccentColor;
  padding-top: 3%;
  font-size: clamp(0.125em, 2vw, 0.35em);
}

.AboutMeTools {
  align-self: center;
  display: grid;
  grid-template-columns: repeat(2, minmax(100px, 300px));
  font-size: clamp(0.125em, 1vw, 0.35em);
  padding-left: 10px;
}

.MyExperience {
  color: $LightThemePrimaryColor;

  display: flex;
  flex-direction: column;

  font-size: clamp(0.125em, 1vw, 0.35em);
  overflow: scroll;
}

.MyExperienceHeader {
  font-size: 2em;
  color: $Yellow;
  padding-bottom: 2.5%;
}

.MyExperienceContainer {
  height: 100%;
  align-self: center;
  width: 75%;
  overflow: scroll;

  @media screen and (max-width: 600px) {
    width: 100%;
  }
}

.MuiTabs-vertical {
  //Button class
  display: flex;
  flex-direction: row;
  overflow: scroll;

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }

  // border-style: solid;
  // border-color: white;
}

.MuiTabsList-vertical {
  display: flex;
  flex-direction: column;
  margin-right: 5%;

  @media screen and (max-width: 600px) {
    flex-direction: row;
    overflow-x: scroll;
    margin-right: 0%;
    height: 41px;
  }

  // border-style: solid;
  // border-color: lightgray;
}

.MuiTab-root {
  width: 150px;
  height: 50px;

  color: $LightThemePrimaryColor;
  background-color: $DarkThemePrimaryColor;

  border-width: 0px;
  border-right-width: 2px;
  border-color: $Yellow;
  flex-shrink: 0;

  @media screen and (max-width: 600px) {
    width: 100px;
    height: 40px;
    border-right-width: 0px;
    border-bottom-width: 2px;
  }
}

.MuiTab-root:hover {
  color: $Yellow;
  background-color: #faeb9e1f;
}

.Mui-selected {
  color: $Yellow;

  background-color: #faeb9e1f;
  border-right-style: solid;
  border-right-width: 2px;

  // border-style: solid;
  // border-color: gray;

  @media screen and (max-width: 600px) {
    border-right-width: 0px;
    border-bottom-width: 2px;
  }
}

.MuiTabPanel-root {
  display: flex;
  flex-direction: column;

  overflow: scroll;

  // border-style: solid;
  // border-color: chartreuse;
}

.MyExperienceAltColor {
  color: $Yellow;
}

.Projects {
  color: $LightThemePrimaryColor;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  font-size: clamp(0.125em, 1vw, 0.35em);
}

.ProjectsHeader {
  font-size: 2em;
  color: $Green;
  padding-bottom: 2.5%;
}

.ProjectContainer {
  display: flex;
  flex-direction: row;
  gap: 1%;
  width: 100%;
}

.ProjectItem {
  border-width: 0;
  max-height: 500px;
  width: 30%;
  height: 500px;
  align-self: center;
  overflow: hidden;
}

.ProjectItem:hover{
  .ProjectItemTextContainer{
    opacity:1;
    visibility: visible;
    animation-name: slidein;
    animation-duration: .5s;
  }
}

.ProjectItemImage-scale{
  height: 500px;
  width: 100%;
}

.ProjectItemTextContainer {
  position: relative;
  visibility: hidden;
  top:-510px;
  padding: 5%;
  background-color: #1f2022f0;
  height: 480px;
}

.ProjectItemTextHeader{
  font-weight: bold;
  padding-bottom: 5%;
  color: $Green;
}

.center{
  padding-top: 225px;
  margin-bottom: auto;
  height: 50px;
}

.ProjectItemTextPara{
  font-size: clamp(0.35em, 3.75vw, 0.75em);
}

.ProjectTag{
  background-color: $Green;
  border-radius: 50px;
  padding: 8px;
  width: fit-content;
  font-size: .8em;
  color: $DarkThemePrimaryColor;
  margin: 5%;
}

@keyframes slidein {
  from {
    margin-top: 150%;
    height: 50px;
  }
  to {
    margin-top: 0%;
    height: 480px;
  }
}

.ProjectsParagraph {
  padding-bottom: 2.5%;
}

.ProjectsLink {
  color: $Green !important;
}

.ContactMe {
  color: $LightThemePrimaryColor;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  font-size: clamp(0.125em, 1vw, 0.35em);
}

.ContactMeHeader {
  font-size: 2em;
  color: $Blue;
  padding-bottom: 2.5%;
}

.ContactMeParagraph {
  display: flex;
  flex-direction: column;
  padding-bottom: 2%;
}

.ContactMeLinksContainer {
  display: flex;
  flex-direction: row;
  gap: 10%;
  font-size: 3em;
  align-self: center;
}

.ContactMeLink {
  color: $Blue !important;
}

.ContactMeLink:hover {
  transform: translateY(-10px);
}

.ContactMeHireMe {
  font-size: 2em;
  padding-top: 20%;
  color: $Purple;

  @media screen and (max-width: 600px) {
    align-self: center;
  }
}

.ContactMeResumeDownload {
  width: fit-content;
  font-size: 1em;

  padding: 1%;
  border-style: solid;
  border-width: 2px;
  border-radius: 10px;

  @media screen and (max-width: 600px) {
    align-self: center;
  }
}

.ContactMeResumeDownload:hover {
  color: $Purple;
}
